import Layout from '../components/Layout'
const marginS = '15px';
const imageSizeM = '270px';
const marginL = ' 75px';
const marginXL = '120px';
const button = {
	backgroundColor: 'rgba(100,100,100,0.35)',
	padding: '0.5rem 1rem',
	margin: '0.5rem auto 0 auto',
	color: 'white',
};

const collections_block = {
	backgroundRepeat: 'no-repeat',
	backgroundSize: 'cover',
	display: 'flex',
	justifyContent: 'center',
	flexDirection: 'column',
	backgroundPosition: 'center',
	minHeight: '25vh',
	maxWidth: '1024px',
    height: '45%',
};
const study_block__text = {
	margin: '0 auto',
    paddingLeft: marginL,
    paddingRight: marginL,
	color: 'white',
	textAlign: 'center',
	fontWeight: 'bold',
};
const collections_block__image = {
	maxHeight: imageSizeM,
};

const master_class1 = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.0)),url(https://sun9-70.userapi.com/c857036/v857036960/d38c9/ZAbUvYg9hvE.jpg)',
	},
};
const master_class2 = {
	...collections_block,
	...{
		backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.0)),url(https://sun9-34.userapi.com/c857428/v857428960/1b6675/BASkOvJrKe4.jpg)',
	},
};
const study_wrapper = {
        height:'calc(100vh - 180px)',
        display: 'flex',
		margin: '0 auto',
        flexDirection: 'column',
        justifyContent:'space-evenly'
}
const Study = () => (
    <Layout solid>
    <div style={study_wrapper}>
     <div style={master_class1}>
			<div style={study_block__text}>ГАЙД ПО СОЗДАНИЮ СРЕЗОВ КАМНЕЙ (+видео)</div>
			<a style={button} href={'/study1'}>
				Посмотреть
			</a>
		</div>
		<div style={mastee_class2}>
			<div style={study_block__text}>ВИДЕО-КУРС ПО ВИТРАЖНЫМ ПОДСТАКАННИКАМ</div>
			<a style={button} href={'/study2'}>
				Посмотреть
			</a>
		</div>
    </div>
   
    </Layout>
)
export default Study;